import { HintState } from '../../constants';
import { delayRunnable, removeRunnable } from '../../utils';
import { ObservableModel } from '../observable-model';

export class HintModel extends ObservableModel {
  constructor() {
    super('HintModel');

    this._visible = false;
    this._state = null;

    this.makeObservable();
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  get visible() {
    return this._visible;
  }

  set visible(value) {
    this._visible = value;
    this.stopVisibilityTimer();
  }

  get timerAlreadyStarted() {
    return this._visibilityTimer;
  }

  destroy() {
    this.stopVisibilityTimer();
  }

  startVisibilityTimer() {
    this._visibilityTimer = delayRunnable(CI_API.Globals.PARAMS.hint_on_idle, () => {
      this._visible = true;
    });
  }

  stopVisibilityTimer() {
    removeRunnable(this._visibilityTimer);
    this._visibilityTimer = null;
  }

  initialize() {
    this._state = CI_API.Globals.PARAMS.intro ? HintState.Menu : HintState.Board;
  }
}
