/* eslint-disable class-methods-use-this */
import { getItemImageConfig } from '../../../configs/image-configs';
import { InputPriority } from '../../../constants';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class CellView extends Container {
  constructor(uuid, config) {
    super();

    const { row, col } = config;

    this.cellClick = new Phaser.Signal();
    this._row = row;
    this._col = col;
    this._uuid = uuid;
    this._config = config;

    this._build();

    this.switchInput(false);
  }

  get uuid() {
    return this._uuid;
  }

  get row() {
    return this._row;
  }

  get col() {
    return this._col;
  }

  bounceItem() {
    this.game.add.tween(this._item.scale).to({ x: 1.1, y: 1.1 }, 200, Phaser.Easing.Sinusoidal.InOut, true, 0, 4, true);
  }

  switchInput(value) {
    this._hitArea.inputEnabled = value;
  }

  _build() {
    this._buildItem();
    this._buildHitArea();
  }

  _buildItem() {
    const { item: key } = this._config;
    const item = makeImage(getItemImageConfig(key));

    this.addChild((this._item = item));
  }

  _buildHitArea() {
    const w = 188;
    const h = 188;

    const gr = this.game.add
      .graphics()
      .beginFill(0x00ff00, 0)
      .drawRect(-w / 2, -h / 2, w, h)
      .endFill();

    this.addChild((this._hitArea = gr));

    this._hitArea.inputEnabled = true;
    this._hitArea.input.priorityID = InputPriority.Game;
    this._hitArea.events.onInputDown.addOnce(() => {
      this.cellClick.dispatch(this.uuid);
    }, this);
  }
}
