import { CellState } from '../../../constants';
import { ObservableModel } from '../../observable-model';

export class CellModel extends ObservableModel {
  constructor(item, row, col) {
    super('CellModel');

    this._item = item;
    this._row = row;
    this._col = col;
    this._state = CellState.Unknown;

    this.makeObservable('_state');
  }

  get item() {
    return this._item;
  }

  get row() {
    return this._row;
  }

  get col() {
    return this._col;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }
}
