import { lego } from '@armathai/lego';
import { menuModelGuard } from '../../guards/game/menu-model-guard';
import { updateNearestItemCommand } from '../game/menu/update-nearest-item-command';

export function onNearestItemUpdateCommand(uuid) {
  lego.command
    .payload(uuid)
    .guard(menuModelGuard)
    .execute(updateNearestItemCommand);
}
