import { BoardTheme, ItemType } from '../constants';

export function getBoardConfig(theme) {
  const configs = {
    [BoardTheme.Tropic]: {
      icon: BoardTheme.Tropic,
      winningType: ItemType.Palm,
      layout: [
        [ItemType.Palm, ItemType.Helmet, ItemType.Palm],
        [ItemType.Coin, ItemType.Helmet, ItemType.Palm],
        [ItemType.Hearts, ItemType.Cache, ItemType.Clubs]
      ]
    },
    [BoardTheme.Treasure]: {
      icon: BoardTheme.Treasure,
      winningType: ItemType.Coin,
      layout: [
        [ItemType.Coin, ItemType.Helmet, ItemType.Coin],
        [ItemType.Helmet, ItemType.Cache, ItemType.Cache],
        [ItemType.Hearts, ItemType.Coin, ItemType.Clubs]
      ]
    },
    [BoardTheme.Football]: {
      icon: BoardTheme.Football,
      winningType: ItemType.Helmet,
      layout: [
        [ItemType.Coin, ItemType.Helmet, ItemType.Cache],
        [ItemType.Helmet, ItemType.Spades, ItemType.Spades],
        [ItemType.Hearts, ItemType.Helmet, ItemType.Clubs]
      ]
    }
  };

  return configs[theme];
}
