/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getBackgroundGridConfig } from '../../configs/grid-configs';
import { getBackgroundViewImageConfig } from '../../configs/image-configs';
import { menuConfig } from '../../configs/menu-config';
import { ModelEvents } from '../../events/model-events';
import { makeImage, makePixel, tweenTint, tweenToCell } from '../../utils';

export class BackgroundView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this._whiteBg = null;
    this._greenBg = null;

    this.build(this.getGridConfig());

    lego.event.on(ModelEvents.BoardModel.ThemeUpdate, this._onBoardModelThemeUpdate, this);
  }

  get name() {
    return 'BackgroundView';
  }

  getGridConfig() {
    return getBackgroundGridConfig();
  }

  build(config) {
    super.build(config);

    this._greenBg = makePixel({ alpha: 1, tint: 0x42c668 });
    this.setChild('bg', this._greenBg);

    this._whiteBg = makePixel({ alpha: 1, tint: 0xffffff });
    this.setChild('white_bg', this._whiteBg);

    this._greenBg.inputEnabled = true;
    this._whiteBg.inputEnabled = true;
    this._greenBg.name = 'background';
    this._whiteBg.name = 'background';
  }

  _onBoardModelThemeUpdate(theme) {
    const { frame } = menuConfig.find(item => item.theme === theme);
    this._createBg(frame);

    const maskRect = this.game.add
      .graphics()
      .beginFill(0xff0000, 0.8)
      .drawRect(0, 0, 10, 10)
      .endFill();

    this.setChild('mask', maskRect);
    this._bg.mask = maskRect;
  }

  _createBg(key) {
    this._bg && this._bg.destroy();
    this._bg = makeImage(getBackgroundViewImageConfig(key));
    const { width, height } = this._bg;
    this._bg.getBounds = () => {
      return new Phaser.Rectangle(-width / 2 + 20, -height / 2 + 20, width - 40, height - 40);
    };
    this.setChild('mini_bg', this._bg);
    tweenToCell(this, this._bg, 'bg', 800).onStart.add(() => {
      tweenTint({ target: this._bg, to: '0xaaaaaa', from: '0xffffff' });
    });
  }
}
