import { lego } from '@armathai/lego';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { menuModelGuard } from '../../guards/game/menu-model-guard';
import { hideHintCommand } from '../ad/hint/hide-hint-command';
import { stopHintVisibilityTimerCommand } from '../ad/hint/stop-hint-visibility-timer-command';
import { setTutorialSkipCommand } from '../ad/tutorial/set-tutorial-skip-command';
import { initializeBoardModelCommand } from '../game/board/initialize-board-model-command';
import { destroyMenuModelCommand } from '../game/menu/destroy-menu-model-command';

export function onMenuItemClickCommand(theme) {
  lego.command
    .payload(theme)
    .execute(initializeBoardModelCommand)

    .guard(menuModelGuard)
    .execute(destroyMenuModelCommand)

    .guard(tutorialModelGuard)
    .execute(setTutorialSkipCommand)

    .guard(hintModelGuard)
    .execute(hideHintCommand, stopHintVisibilityTimerCommand);
}
