import { lego } from '@armathai/lego';
import { GameModelState } from '../../constants';
import { timerParamGuard } from '../../guards/ad/timer-param-guard';
import { initializeTimerCommand } from './timer/initialize-timer-command';

export function onGameModelStateUpdateCommand(state) {
  lego.command
    //
    .guard(timerParamGuard, () => state === GameModelState.Game)
    .execute(initializeTimerCommand);
}
