import { lego, not } from '@armathai/lego';
import { allItemsFoundGuard } from '../../guards/game/all-items-found-guard';
import { itemsToCtaCompleteCommand } from '../ad/cta/items-to-cta-complete-command';
import { loseCommand } from '../ad/cta/lose-command';

export function movesFinishedCommand() {
  lego.command
    //
    .guard(allItemsFoundGuard)
    .execute(itemsToCtaCompleteCommand)

    .guard(not(allItemsFoundGuard))
    .execute(loseCommand);
}
