import { CellState } from '../../constants';
import { store } from '../../models/store';

export function allItemsFoundGuard() {
  const { cells, config } = store.game.boardModel;
  const cellsArr = [];
  const { winningType } = config;

  cells.forEach(row => cellsArr.push(...row));
  const winningItems = cellsArr.filter(
    cell => cell.item === winningType && (cell.state === CellState.Opened || cell.state === CellState.Erase)
  );

  return winningItems.length === 3;
}
