import { ObservableModel } from '../observable-model';

export class PlayerModel extends ObservableModel {
  constructor() {
    super('PlayerModel');

    this._moves = null;

    this.makeObservable('_moves');
  }

  get moves() {
    return this._moves;
  }

  set moves(value) {
    this._moves = value;
  }

  initialize() {
    this._moves = CI_API.Globals.PARAMS.moves;
  }
}
