import { FONT1, FONT2 } from '../constants';

// TUTORIAL
export function getTutorialTextConfig(text) {
  return {
    text,
    style: {
      font: FONT2,
      fontSize: 56,
      fill: '#42c46a'
    }
  };
}

export function getSecondSequenceTutorialTextConfig(text, color) {
  return {
    text,
    style: {
      font: FONT2,
      fontSize: 76,
      fill: color
    }
  };
}

export function getSecondSequenceTutorialCountTextConfig(text, color) {
  return {
    text: {
      text: `${text}`,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    },
    style: {
      font: FONT2,
      fontSize: 76,
      fill: color
    }
  };
}

