import { lego } from '@armathai/lego';
import { boardModelGuard } from '../../guards/game/board-model-guard';
import { menuModelGuard } from '../../guards/game/menu-model-guard';
import { destroyBoardModelCommand } from './board/destroy-board-model-command';
import { destroyMenuModelCommand } from './menu/destroy-menu-model-command';

export function destroyGameModelCommand() {
  lego.command.guard(boardModelGuard).execute(destroyBoardModelCommand);
  lego.command.guard(menuModelGuard).execute(destroyMenuModelCommand);
}
