/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getTimerTextConfig } from '../../configs/text-configs';
import { TIMER_PULSE_TIME } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { Container } from '../../utils/container';
import { makeText, msToTime } from '../../utils/index';

export class TimerView extends Container {
  constructor() {
    super();
    this._isFlashing = false;

    lego.event.on(ModelEvents.TimerModel.RemainingUpdate, this._onTimerUpdate, this);
    lego.event.on(ModelEvents.TimerModel.StoppedUpdate, this._onTimerStoppedUpdate, this);

    this.alpha = 0;
    this._build();
    this._show();
    this.setTime(CI_API.Globals.PARAMS.time);
  }

  get name() {
    return 'TimerView';
  }

  setTime(value) {
    const time = msToTime(value);
    if (value <= TIMER_PULSE_TIME) {
      this.startPulsing();
    }

    this._label.setText({
      text: `${time.minutesStr}:${time.secondsStr}`,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    });
  }

  startPulsing() {
    if (!this._isFlashing) {
      this._isFlashing = true;
      this._label.tint = 0xf3382a;
      this._pulseTween = CI_API.game.add
        .tween(this._label.scale)
        .to({ x: 1.1, y: 1.1 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
      this._pulseTween.universal = true;
    }
  }

  stopPulsing() {
    if (this._isFlashing) {
      this._isFlashing = false;
      CI_API.game.tweens.remove(this._pulseTween);
    }
  }

  destroy() {
    super.destroy();
  }

  _onTimerStoppedUpdate(stopped) {
    if (stopped) {
      this._hide();
    }
  }

  _hide() {
    this.game.add
      .tween(this)
      .to({ alpha: 0 }, 300, Phaser.Easing.Cubic.InOut, true)
      .onComplete.add(() => {
        this.destroy();
      });
  }

  _show() {
    this.game.add.tween(this).to({ alpha: 1 }, 300, Phaser.Easing.Cubic.InOut, true, 500);
  }

  _onTimerUpdate(newValue) {
    this.setTime(newValue);
  }

  _build() {
    this.addChild((this._label = this._buildTimerText()));
  }

  _buildTimerText() {
    const text = makeText(getTimerTextConfig());
    text.anchor.set(0.5);
    return text;
  }
}
