import { lego, not } from '@armathai/lego';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { allItemsFoundGuard } from '../../guards/game/all-items-found-guard';
import { movesFinishedGuard } from '../../guards/player/moves-finished-guard';
import { itemsToCtaCompleteCommand } from '../ad/cta/items-to-cta-complete-command';
import { hideHintCommand } from '../ad/hint/hide-hint-command';
import { startHintVisibilityTimerCommand } from '../ad/hint/start-hint-visibility-timer-command';
import { stopHintVisibilityTimerCommand } from '../ad/hint/stop-hint-visibility-timer-command';
import { eraseCellCompleteCommand } from '../game/board/erase-cell-complete-command';
import { movesFinishedCommand } from '../game/moves-finished-command';
import { decreaseMovesCommand } from '../player/decrease-moves-command';

export function onEraseAnimationCompleteCommand(uuid) {
  lego.command
    //
    .execute(decreaseMovesCommand)

    .payload(uuid)
    .execute(eraseCellCompleteCommand)

    .guard(movesFinishedGuard)
    .execute(movesFinishedCommand)

    .guard(not(movesFinishedGuard), allItemsFoundGuard)
    .execute(itemsToCtaCompleteCommand)

    .guard(hintModelGuard)
    .execute(hideHintCommand, stopHintVisibilityTimerCommand, startHintVisibilityTimerCommand);
}
