/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getUIGridConfig } from '../../configs/grid-configs';
import { GameModelState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { hideToBottom, postRunnable, showFromBottom, showFromRight, tweenToCell } from '../../utils';
import { MovesBarView } from './moves-bar-view';
import { PersistentCTAView } from './persistent-cta-view';
import { TaskComponent } from './task-component';
import { TimerView } from './timer-view';

export class UIView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event
      .on(ModelEvents.AdModel.PersistentCtaUpdate, this._onPersistentCtaUpdate, this)
      .on(ModelEvents.GameModel.TimerUpdate, this._onTimerUpdate, this)
      .on(ModelEvents.GameModel.StateUpdate, this._onGameModelStateUpdate, this)
      .on(ModelEvents.CtaModel.PreVisibleUpdate, this._onCtaPreVisibleUpdate, this);
  }

  get name() {
    return 'UIView';
  }

  getGridConfig() {
    return getUIGridConfig();
  }

  _buildMovesBar() {
    this.setChild('moves_bar', (this._movesBar = new MovesBarView()));
    postRunnable(() => {
      this._movesBar.alpha = 1;
      LP(showFromBottom, showFromRight).call(null, this._movesBar);
    });
  }

  _onGameModelStateUpdate(state) {
    switch (state) {
      case GameModelState.Game:
        this._buildTaskComponent();
        this._buildMovesBar();
        if (this._persistentCtaView) {
          this._updatePersistentCTAPosition();
        }
        break;
      case GameModelState.Menu:
        //
        break;

      default:
        break;
    }
  }

  _onCtaPreVisibleUpdate(value) {
    if (value) {
      this._movesBar && hideToBottom(this._movesBar, 200);
      this._task && this._task.hide();
    }
  }

  // PERSISTENT
  _onPersistentCtaUpdate(persistentCta) {
    persistentCta ? this._buildPersistentCta() : this._destroyPersistentCta();
  }

  _updatePersistentCTAPosition() {
    tweenToCell(this, this._persistentCtaView, 'p_cta');
  }

  _buildPersistentCta() {
    this.setChild('p_cta_menu', (this._persistentCtaView = new PersistentCTAView()));
    postRunnable(() => {
      this._persistentCtaView.alpha = 1;
      showFromBottom(this._persistentCtaView, 200);
    });
  }

  _destroyPersistentCta() {
    hideToBottom(this._persistentCtaView).onComplete.add(() => {
      this._persistentCtaView.destroy();
    });
  }

  // TIMER
  _onTimerUpdate(timer) {
    if (CI_API.Globals.PARAMS.timer) {
      timer ? this._buildTimer() : this._destroyTimer();
    }
  }

  _buildTimer() {
    this.setChild('timer', (this._timer = new TimerView()));
  }

  _buildTaskComponent() {
    this.setChild('task', (this._task = new TaskComponent()));
  }

  _destroyTimer() {}
}
