/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCTAGridConfig } from '../../configs/grid-configs';
import { AdStatus, GameOverReasons } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import moduleBindings from '../../module-bindings';
import { CTALoseView } from './cta-lose-view';
import { CTAWinView } from './cta-win-view';

export class CTAView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this.bindings = moduleBindings;
    this._context = lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  getGridConfig() {
    return getCTAGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  build(config) {
    super.build(config);
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this.build(this.getGridConfig());
        this._buildCta();
        break;
      case AdStatus.Game:
        this.removeChildren();
        break;
      default:
    }
  }

  _buildCta() {
    if (store.ad.cta.reason === GameOverReasons.ItemsToCtaReached) {
      this.setChild('container', (this._cta = new CTAWinView()));
    } else {
      this.setChild('container', (this._cta = new CTALoseView()));
    }
  }
}
