// @ts-nocheck
/* eslint-disable class-methods-use-this */
// eslint-disable-next-line max-classes-per-file
import { getCtaWinPlayButtonConfig } from '../../configs/button-configs';
import { getCTAWinGridConfig } from '../../configs/grid-configs';
import { getCTAWinCashConfig, getCTAWinJackpotConfig, getCtaWinViewBgImageConfig } from '../../configs/image-configs';
import { getFallingItemEmitterConfig } from '../../configs/particles-configs';
import { makeImage, postRunnable, showFromBottom, showFromLeft, showFromUp } from '../../utils';
import { ItemFallEmitter } from '../../utils/emmitters/coin-fall/item-fall-emitter';
import { CTABaseView } from './cta-base-view';

export class CTAWinView extends CTABaseView {
  getGridConfig() {
    return getCTAWinGridConfig();
  }

  build() {
    super.build(this.getGridConfig());

    this._buildBg();
    this._buildCoins();
    this._buildJackpotLabel();
    this._buildCashIcon();

    this.buildBlocker(0);
    this.buildPlayButton(getCtaWinPlayButtonConfig(CI_API.Globals.PARAMS.cta_btn_text));
  }

  show() {
    super.show();
    postRunnable(() => {
      this.game.add.tween(this._bg).to({ alpha: 1 }, 400, Phaser.Easing.Cubic.InOut, true);
      LP(showFromUp, this._showCashIcon).call(this, this._cashIcon);

      LP(showFromBottom, showFromLeft)
        .call(this, this._jackpotLabel)
        .onStart.add(() => {
          this._jackpotLabel.alpha = 1;
          this._cashIcon.alpha = 1;
        });
    });
  }

  _showCashIcon() {
    return this.game.add.tween(this._cashIcon.scale).from({ x: 0, y: 0 }, 500, Phaser.Easing.Back.Out, true, 500);
  }

  _buildBg() {
    this.setChild('bg', (this._bg = makeImage(getCtaWinViewBgImageConfig())));
    this._bg.alpha = 0;
  }

  _buildJackpotLabel() {
    this.setChild('win-jackpot', (this._jackpotLabel = makeImage(getCTAWinJackpotConfig())));
    this._jackpotLabel.alpha = 0;
  }

  _buildCashIcon() {
    this.setChild('win-cash', (this._cashIcon = makeImage(getCTAWinCashConfig())));
    this._cashIcon.alpha = 0;
  }

  _buildCoins() {
    this._emitter = new ItemFallEmitter(getFallingItemEmitterConfig());
    this._emitter.emit();
    this.addChild(this._emitter);
  }
}
