/* eslint-disable no-underscore-dangle */
/**
 * Returns a random number between min (inclusive) and max (exclusive)
 */
function getRandom(min, max) {
  return Math.random() * (max - min) + min;
}

export class ItemFallEmitter extends Phaser.Group {
  constructor(config) {
    super(CI_API.game);

    this._config = config;
    this._particles = [];
    this._events = [];
    this._fps = 60;
  }

  _onFocus() {
    if (!this.game) {
      return;
    }

    this.stop();
    this.emit();
  }

  emit() {
    this.game.onFocus.remove(this._onFocus, this);
    this.game.onFocus.add(this._onFocus, this);

    const { count, delay } = this._config;

    for (let i = 0; i < count; i += 1) {
      const dt = getRandom(delay.min, delay.max);
      const spawnEvent = this.game.time.events.add(dt, this._spawn, this);
      this._events.push(spawnEvent);
    }
  }

  stop(removeActives) {
    this.game.onFocus.remove(this._onFocus, this);

    this._events.forEach(e => this.game.time.events.remove(e));

    if (removeActives) {
      this._particles.forEach(p => p.destroy());
      this._particles = [];
    }
  }

  update() {
    super.update();

    this._particles.forEach(p => {
      p.__velocity__.x += this._config.gravity.x;
      p.__velocity__.y += this._config.gravity.y;
      p.x += p.__velocity__.x / this._fps;
      p.y += p.__velocity__.y / this._fps;
      p.rotation += p.__rotation__ / this._fps;
    });
  }

  destroy() {
    this.stop(true);
    super.destroy();
  }

  _spawn() {
    const { particleClass, velocity, area, duration, scale, rotation, repeat } = this._config;

    const velX = getRandom(velocity.x.min, velocity.x.max);
    const velY = getRandom(velocity.y.min, velocity.y.max);
    const sc = getRandom(scale.min, scale.max);
    const rt = getRandom(rotation.min, rotation.max);
    const dt = getRandom(duration.min, duration.max);
    const { x: posX, y: posY } = area.shape.random();
    const config = {
      velocity: { x: velX, y: velY },
      rotation: rt
    };

    // eslint-disable-next-line new-cap
    const p = new particleClass(config);
    p.position.set(posX, posY);
    p.scale.set(sc);
    p.rotation = rt;
    p.onEmit();

    const killEvent = this.game.time.events.add(dt, this._kill, this, p, repeat);

    this._events.push(killEvent);
    this._particles.push(p);
    this.addChild(p);
  }

  _kill(particle, revive) {
    this._particles.splice(this._particles.indexOf(particle), 1);
    particle.destroy();

    if (revive) {
      this._spawn();
    }
  }
}
