import { lego } from '@armathai/lego';
import { HintState } from '../../constants';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { setHintStateCommand } from '../ad/hint/set-hint-state-command';

export function onMenuModelUpdateCommand(menuModel) {
  lego.command
    .guard(hintModelGuard, () => !!menuModel)
    .payload(HintState.Menu)
    .execute(setHintStateCommand);
}
