import { lego } from '@armathai/lego';
import { BoardState } from '../../constants';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { boardModelGuard } from '../../guards/game/board-model-guard';
import { store } from '../../models/store';
import { hideHintCommand } from '../ad/hint/hide-hint-command';
import { startHintVisibilityTimerCommand } from '../ad/hint/start-hint-visibility-timer-command';
import { stopHintVisibilityTimerCommand } from '../ad/hint/stop-hint-visibility-timer-command';
import { setLastInteractionCommand } from '../ad/set-last-interaction-command';

export function userInteractionCommand() {
  lego.command
    .payload(CI_API.game.time.totalElapsedSeconds())
    .execute(setLastInteractionCommand)

    .guard(hintModelGuard)
    .execute(hideHintCommand, stopHintVisibilityTimerCommand)

    .guard(boardModelGuard, hintModelGuard, () => store.game.boardModel.state === BoardState.Idle)
    .execute(startHintVisibilityTimerCommand);
}
