import { CellScale } from '@armathai/phaser2-grid';

export function getBackgroundGridLandscapeConfig() {
  return {
    // debug: { color: 0xd95027 },
    name: 'background',
    cells: [
      {
        name: 'mini_bg',
        bounds: { x: 0.45, y: 0.45, width: 0.1, height: 0.1 },
        scale: CellScale.Fit
      },
      {
        name: 'bg',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Envelop
      },
      {
        name: 'white_bg',
        bounds: { x: 0, y: 0.6, width: 1, height: 0.5 },
        scale: CellScale.Fill
      },
      {
        name: 'mask',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      }
    ]
  };
}

