import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class MenuItemView extends Container {
  constructor(model) {
    super();

    const { left, right } = model.config.neighbors;

    this._leftItemIndex = left;
    this._rightItemIndex = right;
    this.uuid = model.uuid;
    this.onClick = new Phaser.Signal();
    this._theme = model.theme;

    this._build(model);
  }

  destroy() {
    this._content.inputEnabled = false;
    this.game.tweens.removeFrom(this);
    this._content.events.onInputUp.remove(this._onClick, this);
    super.destroy();
  }

  get theme() {
    return this._theme;
  }

  get leftItemIndex() {
    return this._leftItemIndex;
  }

  get rightItemIndex() {
    return this._rightItemIndex;
  }

  switchInput(value) {
    this._content.inputEnabled = value;
  }

  updateDim(fraction) {
    this._dim.alpha = fraction;
  }

  _build({ frame }) {
    const img = makeImage({ frame });
    const dim = makeImage({ frame });
    dim.tint = 0;
    this.addChild((this._content = img));
    this.addChild((this._dim = dim));
    img.scale.set(0.65);
    dim.scale.set(0.65);
    this._content.inputEnabled = false;
    this._content.name = 'menu_item';
    this._content.events.onInputUp.addOnce(this._onClick, this);
  }

  _onClick() {
    this.onClick.dispatch(this._theme, this.uuid);
  }
}
