import { lego } from '@armathai/lego';
import { HintState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { getViewByProperty, handMoveTw, handScaleTw, searchAtlasByFrame } from '../../utils';
import { sample } from '../../utils/array/sample';

export class HintView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/hand.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0.05, 0.05);
    this._getPositionsCallback = this._getMenuHintPositions;

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    lego.event.on(ModelEvents.HintModel.StateUpdate, this._onHintStateUpdate, this);
    this._hide();
  }

  destroy() {
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    this.game.tweens.removeFrom(this);
    this.game.tweens.removeFrom(this.scale);
    super.destroy();
  }

  _onHintStateUpdate(state) {
    switch (state) {
      case HintState.Menu:
        this._getPositionsCallback = this._getMenuHintPositions;
        break;
      case HintState.Board:
        this._getPositionsCallback = this._getGameHintPositions;
        break;

      default:
        break;
    }
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _getGameHintPositions() {
    const boardView = getViewByProperty('name', 'BoardView');
    const { boardModel } = store.game;
    const coveredCells = boardModel.getCoveredCells();
    const winningCells = coveredCells.filter(cell => cell.item === boardModel.config.winningType);
    const { uuid } = sample(winningCells);

    lego.event.emit(ViewEvents.HintView.HintOnCell, uuid);

    const cellView = boardView.getCellByUuid(uuid);
    const pos = this.parent.toLocal(
      cellView.parent.toGlobal(new Phaser.Point(cellView.centerX, cellView.centerY)),
      undefined
    );

    return [pos];
  }

  _getMenuHintPositions() {
    const itemView = getViewByProperty('name', 'MenuView').getNearestItem();
    const pos = this.parent.toLocal(itemView.position, itemView.parent);

    return [pos];
  }

  _show() {
    this._removeTw();
    this._positions = this._getPositionsCallback.call(this);
    this._currentPoint = 0;

    this._firstTimeShow();
  }

  _firstTimeShow() {
    const point = this._positions[this._currentPoint];
    this.scale.set(1);
    this.alpha = 1;
    this.position.set(point.x, point.y);
    this.angle = 0;
    this.visible = true;
    const { x, y, angle } = this;

    this.game.add
      .tween(this)
      .from({ x: x + 50, y: y + 50, angle: angle + 30 }, 400, Phaser.Easing.Cubic.Out, true, 0)
      .onComplete.addOnce(() => {
        this._pointHand();
      });
    this.game.add.tween(this).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.Out, true, 0);
  }

  _pointHand() {
    handScaleTw(this).onComplete.addOnce(() => {
      this._currentPoint += 1;
      if (this._currentPoint === this._positions.length) {
        this._currentPoint = 0;
      }
      this._moveHand(this._positions[this._currentPoint]);
    });
  }

  _moveHand(pos) {
    handMoveTw(this, pos).onComplete.addOnce(() => {
      this._pointHand();
    });
  }

  _hide() {
    this._removeTw();
    this.visible = false;
  }

  _removeTw() {
    this.game.tweens.removeFrom(this.scale);
    this.game.tweens.removeFrom(this);
  }
}
