import { lego } from '@armathai/lego';
import { BoardState, HintState } from '../../constants';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { timerParamGuard } from '../../guards/ad/timer-param-guard';
import { timerModelGuard } from '../../guards/game/timer-model-guard';
import { hideHintCommand } from '../ad/hint/hide-hint-command';
import { setHintStateCommand } from '../ad/hint/set-hint-state-command';
import { startHintVisibilityTimerCommand } from '../ad/hint/start-hint-visibility-timer-command';
import { stopHintVisibilityTimerCommand } from '../ad/hint/stop-hint-visibility-timer-command';
import { setBoardStateCommand } from '../game/board/set-board-state-command';
import { startTimerCommand } from '../game/timer/start-timer-command';

export function onBoardCoverCompleteCommand() {
  lego.command
    //
    .payload(BoardState.Idle)
    .execute(setBoardStateCommand)

    .payload(HintState.Board)
    .guard(hintModelGuard)
    .execute(setHintStateCommand, hideHintCommand, stopHintVisibilityTimerCommand, startHintVisibilityTimerCommand)

    .guard(timerParamGuard, timerModelGuard)
    .execute(startTimerCommand);
}
