import { CellAlign, CellScale } from '@armathai/phaser2-grid';

export function getCTALoseGridLandscapeConfig() {
  return {
    // debug: { color: 0x4b4ad4 },
    name: 'lose',
    cells: [
      {
        name: 'bg',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      },
      {
        name: 'title',
        scale: CellScale.ShowAll,
        bounds: { x: 0.2, y: 0.565, width: 0.6, height: 0.2 }
      },
      {
        name: 'btn',
        bounds: { x: 0.325, y: 0.82, width: 0.36, height: 0.16 },
        align: CellAlign.CenterTop
      },
      {
        name: 'icon',
        scale: CellScale.ShowAll,
        bounds: { x: 0.25, y: 0.15, width: 0.5, height: 0.4 }
      },
      {
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        name: 'blocker',
        scale: CellScale.Fill
      }
    ]
  };
}

