import { ObservableModel } from '../../observable-model';

export class MenuItemModel extends ObservableModel {
  constructor(config) {
    super('MenuItemModel');
    this._config = config;

    this._theme = config.theme;
    this._frame = config.frame;
  }

  get theme() {
    return this._theme;
  }

  get frame() {
    return this._frame;
  }

  get config() {
    return this._config;
  }
}
