import { getBoardConfig } from '../../../configs/board-configs';
import { BoardState, CellState } from '../../../constants';
import { ObservableModel } from '../../observable-model';
import { CellModel } from './cell-model';

export class BoardModel extends ObservableModel {
  constructor() {
    super('BoardModel');

    this._theme = null;
    this._config = null;
    this._cells = null;
    this._state = BoardState.Unknown;

    this.makeObservable('_theme', '_cells', '_state');
  }

  get config() {
    return this._config;
  }

  get theme() {
    return this._theme;
  }

  get cells() {
    return this._cells;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  getCellByUUID(uuid) {
    for (let i = 0; i < this._cells.length; i += 1) {
      const cell = this._cells[i].find(c => c && c.uuid === uuid);
      if (cell) {
        return cell;
      }
    }
    return null;
  }

  getCellsByType(type) {
    const cells = [];

    for (let i = 0; i < this.cells.length; i += 1) {
      const row = this.cells[i];
      cells.push(...row.filter(cell => cell.item === type));
    }

    return cells;
  }

  getCoveredCells() {
    const cells = [];

    for (let i = 0; i < this.cells.length; i += 1) {
      const row = this.cells[i];
      cells.push(...row.filter(cell => cell.state === CellState.Closed));
    }

    return cells;
  }

  initialize(theme) {
    this._theme = theme;
    this._config = getBoardConfig(theme);
    this._initCells();
    this._cover();
  }

  erase(uuid) {
    const cell = this.getCellByUUID(uuid);
    cell.state = CellState.Erase;
    this._state = BoardState.Erase;
  }

  eraseComplete(uuid) {
    const cell = this.getCellByUUID(uuid);
    cell.state = CellState.Opened;
    this._state = BoardState.Idle;
  }

  _cover() {
    this._cells.forEach(row => {
      row.forEach(cell => {
        cell.state = CellState.Closed;
      });
    });

    this._state = BoardState.Cover;
  }

  _initCells() {
    const { layout } = this._config;
    const cells = [];
    layout.forEach((row, rowIndex) => {
      cells.push(row.map((data, colIndex) => new CellModel(data, rowIndex, colIndex)));
    });

    this._cells = cells;
  }
}
