import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getUIGridLandscapeConfig() {
  const taskBounds = CI_API.Globals.PARAMS.timer
    ? { x: 0, y: 0.6, width: 0.3, height: 0.23 }
    : { x: 0, y: 0.6, width: 0.5, height: 0.23 };

  const taskPadding = CI_API.Globals.PARAMS.timer
    ? { x: 0.1, y: -0.2, width: 0.8, height: 1.4 }
    : { x: 0.1, y: 0, width: 0.8, height: 1 };

  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'task',
        // debug: { color: 0x00ffff },
        scale: CellScale.ShowAll,
        bounds: taskBounds,
        padding: taskPadding
      },
      {
        name: 'timer',
        // debug: { color: 0x00ffff },
        scale: CellScale.ShowAll,
        bounds: { x: 0.3, y: 0.63, width: 0.2, height: 0.18 },
        padding: { x: 0.1, width: isSquareLikeScreen() ? 0.7 : 0.8 }
      },
      {
        name: 'moves_bar',
        // debug: { color: 0xff00ff },
        bounds: { x: 0.5, y: 0.87, width: 0.5, height: 0.1 },
        padding: { x: 0.1, width: 0.8 }
      },
      {
        name: 'p_cta',
        // debug: { color: 0x4287f5 },
        bounds: { x: 0, y: 0.85, width: 0.5, height: 0.12 },
        padding: { x: 0.1, width: 0.8 }
      },
      {
        name: 'p_cta_menu',
        bounds: { x: 0, y: 0.85, width: 0.3, height: 0.12 },
        padding: { x: 0.1, width: 0.8 }
      }
    ]
  };
}

export function getUIGridPortraitConfig() {
  const uiY = isSquareLikeScreen() ? 0.26 : 0.27;

  const timerPadding = isSquareLikeScreen()
    ? { x: 0.1, y: 0, width: 0.55, height: 1 }
    : { x: 0.2, y: 0, width: 0.75, height: 1 };
  const timerAlign = isSquareLikeScreen() ? CellAlign.Center : CellAlign.Center;

  let taskBounds = { x: 0, y: uiY, width: 0.47, height: 0.06 };
  let taskAlign = isSquareLikeScreen() ? CellAlign.LeftCenter : CellAlign.Center;
  let taskPadding = isSquareLikeScreen() ? { x: 0.35, y: -0.3, height: 1.6 } : { x: 0.1, y: -0.3, height: 1.6 };

  if (!CI_API.Globals.PARAMS.timer) {
    taskAlign = CellAlign.Center;
    // @ts-ignore
    taskPadding = { x: 0.1, width: 0.8 };
    taskBounds = { x: 0, y: 0.22, width: 1, height: 0.06 + 0.05 };
  }

  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'timer',
        // debug: { color: 0x00ffff },
        bounds: { x: 0.53, y: uiY, width: 0.47, height: 0.06 },
        padding: timerPadding,
        align: timerAlign
      },
      {
        name: 'task',
        // debug: { color: 0x00ffff },
        bounds: taskBounds,
        padding: taskPadding,
        align: taskAlign
      },
      {
        name: 'moves_bar',
        // debug: { color: 0xff00ff },
        bounds: { x: 0, y: 0.84, height: 0.06 }
      },
      {
        name: 'p_cta',
        // debug: { color: 0x4287f5 },
        bounds: { x: 0, y: 0.92, height: 0.06 }
      },
      {
        name: 'p_cta_menu',
        bounds: { x: 0, y: 0.92, height: 0.06 }
      }
    ]
  };
}
