/* eslint-disable max-classes-per-file */

import { getCoinFallAnimationConfig } from '../../../configs/animation-configs';

class Particle extends Phaser.Sprite {
  constructor(config) {
    super(CI_API.game, 0, 0, '', '');

    const { velocity, rotation } = config;
    this.__velocity__ = velocity;
    this.__rotation__ = rotation;

    this.anchor.set(0.5);
  }

  // eslint-disable-next-line class-methods-use-this
  onEmit() {}
}

export class ItemFallParticle extends Particle {
  constructor(config) {
    super(config);

    this._buildAnimation();
  }

  onEmit() {
    this.animations.play('fall');
  }

  _buildAnimation() {
    getCoinFallAnimationConfig().data.forEach(d => {
      const { name, prefix, start, stop, suffix, zeroPad, frameRate, loop } = d;
      this.animations.add(
        name,
        Phaser.Animation.generateFrameNames(prefix, start, stop, suffix, zeroPad),
        frameRate,
        loop
      );
    });
  }
}
