// @ts-nocheck
/* eslint-disable class-methods-use-this */
// eslint-disable-next-line max-classes-per-file
import { getCtaLoseButtonConfig } from '../../configs/button-configs';
import { getCTALoseGridConfig } from '../../configs/grid-configs';
import { getCTAEmojiConfig, getCtaWinViewBgImageConfig } from '../../configs/image-configs';
import { getLoseCTATitleTextConfig } from '../../configs/text-configs';
import { makeImage, makeText, postRunnable, showFromRight } from '../../utils';
import { CTABaseView } from './cta-base-view';

export class CTALoseView extends CTABaseView {
  getGridConfig() {
    return getCTALoseGridConfig();
  }

  build() {
    super.build(this.getGridConfig());
    this._buildBg();
    this.buildBlocker(0);
    this.buildPlayButton(getCtaLoseButtonConfig('KEEP PLAYING'));
    this.buildEmoji();
    this._buildTile();
  }

  buildEmoji() {
    const emoji = makeImage(getCTAEmojiConfig());
    emoji.alpha = 0;
    this.setChild(`icon`, (this._emoji = emoji));
  }

  show() {
    super.show();
    postRunnable(() => {
      this.game.add.tween(this._bg).to({ alpha: 1 }, 400, Phaser.Easing.Cubic.InOut, true);
      showFromRight(this._emoji).onStart.add(() => {
        this._emoji.alpha = 1;
      });
    });
  }

  _buildBg() {
    this.setChild('bg', (this._bg = makeImage(getCtaWinViewBgImageConfig())));
    this._bg.alpha = 0;
  }

  _buildTile() {
    const label = makeText(getLoseCTATitleTextConfig());
    this.setChild('title', label);
  }
}
