import { ObservableModel } from '../../observable-model';
import { MenuItemModel } from './menu-item-model';

export class MenuModel extends ObservableModel {
  constructor(config) {
    super('MenuModel');

    this._config = config;
    this._items = null;
    this._nearestItem = null;

    this.initialize();

    this.makeObservable('_nearestItem');
  }

  get items() {
    return this._items;
  }

  get nearestItem() {
    return this._nearestItem;
  }

  set nearestItem(item) {
    this._nearestItem = item;
  }

  getItemByUuid(uuid) {
    return this._items.find(item => item.uuid === uuid);
  }

  updateNearestItem(uuid) {
    this._nearestItem = this.getItemByUuid(uuid);
  }

  initialize() {
    const items = [];

    this._config.forEach(itemConfig => {
      items.push(new MenuItemModel(itemConfig));
    });

    this._items = items;
  }
}
