import { menuConfig } from '../../configs/menu-config';
import { BoardTheme, GameModelState } from '../../constants';
import { sample } from '../../utils/array/sample';
import { objectValues } from '../../utils/object/object-values';
import { ObservableModel } from '../observable-model';
import { BoardModel } from './board/board-model';
import { MenuModel } from './menu/menu-model';
import { TimerModel } from './timer-model';

export class GameModel extends ObservableModel {
  constructor() {
    super('GameModel');

    this._boardModel = null;
    this._menuModel = null;
    this._timer = null;
    this._state = GameModelState.Unknown;

    this.makeObservable();
  }

  get menuModel() {
    return this._menuModel;
  }

  get boardModel() {
    return this._boardModel;
  }

  get timer() {
    return this._timer;
  }

  get state() {
    return this._state;
  }

  initialize() {
    if (CI_API.Globals.PARAMS.intro) {
      this.initializeMenuModel(menuConfig);
    } else {
      this.initializeBoardModel(sample(objectValues(BoardTheme)));
    }
  }

  // MENU
  initializeMenuModel(config) {
    this._menuModel = new MenuModel(config);

    this._state = GameModelState.Menu;
  }

  destroyMenuModel() {
    this._menuModel.destroy();
    this._menuModel = null;
  }

  // BOARD
  initializeBoardModel(theme) {
    this._boardModel = new BoardModel();
    this._boardModel.initialize(theme);

    this._state = GameModelState.Game;
  }

  // TIMER
  initializeTimer() {
    this._timer = new TimerModel();
  }

  destroyTimer() {
    if (this._timer) {
      this._timer.destroy();
      this._timer = null;
    }
  }

  destroyBoardModel() {
    this._boardModel.destroy();
    this._boardModel = null;
  }

  destroy() {
    this.destroyTimer();
    this.destroyBoardModel();
  }
}
