export function getBackgroundViewImageConfig(key) {
  return {
    frame: key
  };
}

export function getCtaWinViewBgImageConfig() {
  return {
    frame: `bg/cta_win_bg.jpg`
  };
}

export function getCTAWinJackpotConfig() {
  return {
    frame: `ui/jackpot.png`
  };
}

export function getCTAWinCashConfig() {
  return {
    frame: `ui/cta_cash.png`
  };
}

export function getCTAEmojiConfig() {
  return {
    frame: `ui/cta_emoji.png`
  };
}

export function getLogoImageConfig() {
  return {
    frame: 'ui/logo'
  };
}

export function getCardFrameImageConfig() {
  return {
    frame: 'board/card_frame.png'
  };
}

export function getItemImageConfig(key) {
  return {
    frame: `board/items/${key}.png`
  };
}

export function getTutorialItemImageConfig(key) {
  return {
    frame: `board/items/${key}.png`
  };
}

export function getRightArrowImageConfig() {
  return {
    frame: `ui/arrow.png`
  };
}

export function getRightArrowDownImageConfig() {
  return {
    frame: `ui/arrow.png`,
    scale: { x: 0.9, y: 0.9 }
  };
}

export function getLeftArrowImageConfig() {
  return {
    frame: `ui/arrow.png`,
    scale: { x: -1, y: 1 }
  };
}

export function getLeftArrowDownImageConfig() {
  return {
    frame: `ui/arrow.png`,
    scale: { x: -0.9, y: 0.9 }
  };
}
