/* eslint-disable class-methods-use-this */
import { getTutorialItemImageConfig } from '../../configs/image-configs';
import {
  getSecondSequenceTutorialCountTextConfig,
  getSecondSequenceTutorialTextConfig
} from '../../configs/text-configs';
import { store } from '../../models/store';
import { hideToBottom, makeImage, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class TaskComponent extends Container {
  constructor() {
    super();

    this.alpha = 0;
    this._build();
    this._show();
  }

  rebuild() {
    this._label.destroy();
    this._countLabel.destroy();
    this._buildLabel();
  }

  hideLabel() {
    return hideToBottom(this);
  }

  hide() {
    this.game.add
      .tween(this)
      .to({ alpha: 0 }, 300, Phaser.Easing.Cubic.InOut, true)
      .onComplete.add(() => {
        this.destroy();
      });
  }

  _show() {
    this.game.add.tween(this).to({ alpha: 1 }, 300, Phaser.Easing.Cubic.InOut, true, 500);
  }

  _build() {
    this._buildLabel();
    this._buildIcon();
  }

  _buildLabel() {
    const label = makeText(getSecondSequenceTutorialTextConfig('Tutorial Text 2', LP('#ffffff', '#ffffff')));
    label.anchor.set(0.5);

    const countLabel = makeText(getSecondSequenceTutorialCountTextConfig('x3', LP('#ffffff', '#ffffff')));
    countLabel.anchor.set(0.5);
    countLabel.position.set(label.width + 120, 0);

    this.addChild((this._label = label));
    this.addChild((this._countLabel = countLabel));
  }

  _buildIcon() {
    const icon = makeImage(getTutorialItemImageConfig(store.game.boardModel.config.winningType));
    icon.position.set(this._label.width + 5, 0);
    this.addChild(icon);
  }
}
