/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getMenuGridConfig } from '../../../configs/grid-configs';
import { getMenuTitleTextConfig } from '../../../configs/text-configs';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { hideToLeft, hideToRight, hideToUp, makeText, postRunnable } from '../../../utils';
import { CarouselComponent } from './carousel-component';
import { MenuItemView } from './menu-item-view';

export class MenuView extends Phaser2Grid {
  constructor(menuModel) {
    super(CI_API.game);
    this._items = [];
    this._title = null;
    this._carousel = null;

    super.build(this.getGridConfig());

    this._buildItems(menuModel.items);

    lego.event.on(ModelEvents.MenuModel.NearestItemUpdate, this._onNearestItemUpdate, this);
  }

  getGridConfig() {
    return getMenuGridConfig();
  }

  get name() {
    return 'MenuView';
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    !!this._carousel && this._carousel.rebuild();
    !!this._carousel && this._carousel.focusOnNearest(true);
    super.rebuild(config);
  }

  destroy() {
    const centerItem = this.getNearestItem();
    const { leftItemIndex, rightItemIndex } = centerItem;
    const { rightArrow, leftArrow } = this._carousel;
    const leftItem = this._items[leftItemIndex];
    const rightItem = this._items[rightItemIndex];

    // hideToRight(this._right, 0, 300);
    // hideToLeft(this._left, 0, 300);
    hideToUp(this._title, 0, 150);
    hideToUp(centerItem, 0, 500);
    hideToRight(rightItem, 0, 500);
    hideToRight(rightArrow, 0, 200);
    hideToLeft(leftArrow, 0, 200);

    hideToLeft(leftItem, 0, 500).onComplete.add(() => {
      super.destroy();
    });
  }

  getNearestItem() {
    return this._carousel.nearestItem;
  }

  getItemByUuid(uuid) {
    return this._items.find(item => item.uuid === uuid);
  }

  _onNearestItemUpdate(item) {
    const {
      config: { title }
    } = item;
    this._updateTitle(title);
  }

  _updateTitle(text) {
    this._title.setText(text);
    this.setChild('title', this._title);
  }

  _buildItems(itemModels) {
    this._addItems(itemModels);
    this._addCarousel();
    this._addTitle();
  }

  _addItems(itemModels) {
    itemModels.forEach(itemModel => {
      const item = new MenuItemView(itemModel);
      this._items.push(item);
      item.onClick.add(this._onItemClick, this);
    });
  }

  _addCarousel() {
    this.setChild('carousel', (this._carousel = new CarouselComponent(this._items)));
    this._carousel.nearestItemUpdate.add(this._onCarouselNearestItemUpdate, this);
    this._carousel.focusOnNearestComplete.add(this._onFocusOnNearestComplete, this);
    postRunnable(() => this._onCarouselNearestItemUpdate(this._carousel.nearestItem.uuid));
  }

  _addTitle() {
    this.setChild('title', (this._title = makeText(getMenuTitleTextConfig(''))));
  }

  _onItemClick(theme, uuid) {
    lego.event.emit(ViewEvents.MenuViewEvent.OnMenuItemClick, theme, uuid);
  }

  _onCarouselNearestItemUpdate(uuid) {
    lego.event.emit(ViewEvents.MenuViewEvent.OnNearestItemUpdate, uuid);
  }

  _onFocusOnNearestComplete() {
    lego.event.emit(ViewEvents.MenuViewEvent.onFocusOnNearestComplete);
  }
}
