import { lego } from '@armathai/lego';
import { getMovesBarBgPatchConfig } from '../../configs/ninepatch-configs';
import { getMovesTextConfig } from '../../configs/text-configs';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { fitText, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class MovesBarView extends Container {
  constructor() {
    super();

    lego.event.on(ModelEvents.PlayerModel.MovesUpdate, this._updateLabel, this);

    this.alpha = 0;
    this._build();
  }

  _build() {
    this._buildBg();
    this._buildLabel();
  }

  _buildBg() {
    const bg = makeNinePatch(getMovesBarBgPatchConfig());
    this.addChild((this._bg = bg));
  }

  _buildLabel() {
    const label = makeText(getMovesTextConfig());
    label.anchor.set(0.5);
    this.addChild((this._label = label));
    this._updateLabel(store.player.moves);
  }

  _updateLabel(value) {
    this._label.text = {
      text: `Moves Left`,
      macros: { '{{0}}': value },
      ignoreLocalization: false,
      toString: function toString() {
        return this;
      }
    };

    fitText(this._label, this._bg.width * 0.84);
  }
}
