/* eslint-disable class-methods-use-this */
import { getItemImageConfig } from '../../../configs/image-configs';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class CellFx extends Container {
  constructor(uuid, config) {
    super();

    const { row, col } = config;

    this._row = row;
    this._col = col;
    this._uuid = uuid;
    this._config = config;

    this._build();
  }

  get uuid() {
    return this._uuid;
  }

  get row() {
    return this._row;
  }

  get col() {
    return this._col;
  }

  startHighlight() {
    this.game.add.tween(this._bg).to({ alpha: 0.6 }, 300, Phaser.Easing.Cubic.InOut, true, 0, -1, true);
  }

  stopHighlight() {
    this.game.tweens.removeFrom(this._bg);
    this._bg.alpha = 0;
  }

  _build() {
    this._buildBg();
  }

  _buildItem() {
    const { item: key } = this._config;
    const item = makeImage(getItemImageConfig(key));

    this.addChild(item);
  }

  _buildBg() {
    const w = 188;
    const h = 188;

    const gr = this.game.add
      .graphics()
      .beginFill(0xffffff, 1)
      .drawRect(-w / 2, -h / 2, w, h)
      .endFill();

    this.addChild((this._bg = gr));

    this._bg.alpha = 0;
  }
}
