/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCardFrameImageConfig } from '../../../configs/image-configs';
import { getCardCoverSpineConfig, Spines } from '../../../configs/spine-configs';
import { BoardState, CellState } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { delayRunnable, makeImage, makeSpine, postRunnable, removeRunnable, showFromRight } from '../../../utils';
import { Container } from '../../../utils/container';
import { CellFx } from './cell-fx';
import { CellView } from './cell-view';

export class BoardView extends Container {
  constructor() {
    super();

    this._cells = [];

    this.alpha = 0;
    this._build();

    lego.event.once(ModelEvents.BoardModel.CellsUpdate, this._onBoardCellsUpdate, this);
    lego.event.on(ModelEvents.BoardModel.StateUpdate, this._onBoardStateUpdate, this);
    lego.event.on(ModelEvents.CellModel.StateUpdate, this._onCellStateUpdate, this);
    lego.event.on(ViewEvents.HintView.HintOnCell, this._onHintOnCell, this);
    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    lego.event.on(ModelEvents.AdModel.HintUpdate, this._onHintUpdate, this);
  }

  get name() {
    return 'BoardView';
  }

  getCellByUuid(uuid) {
    for (let i = 0; i < this._cells.length; i += 1) {
      const cell = this._cells[i].find(c => c && c.uuid === uuid);
      if (cell) {
        return cell;
      }
    }
    return null;
  }

  getCellFxByUuid(uuid) {
    return this._fxs.find(fx => fx.uuid === uuid);
  }

  destroy() {
    removeRunnable(this._eraseRunnable);
    super.destroy();
  }

  _onHintOnCell(uuid) {
    this.getCellFxByUuid(uuid).startHighlight();
  }

  _onHintUpdate(value) {
    if (!value) {
      this._stopHighlights();
    }
  }

  _onHintVisibleUpdate(value) {
    if (!value) {
      this._stopHighlights();
    }
  }

  _stopHighlights() {
    this._fxs.forEach(fx => {
      fx.stopHighlight();
    });
  }

  _bounceRightItems() {
    const { boardModel } = store.game;
    const { winningType } = boardModel.config;
    const cells = boardModel.getCellsByType(winningType);
    cells.forEach(cell => {
      const { uuid } = cell;
      this._bounceItem(uuid);
    });
  }

  _switchCellsInput(value) {
    this._cells.forEach(row => {
      row.forEach(cell => {
        cell.switchInput(value);
      });
    });
  }

  _showCover() {
    const tw = this.game.add.tween(this._cover).from({ alpha: 0 }, 300, Phaser.Easing.Sinusoidal.InOut, true, 2000);

    tw.onStart.add(() => {
      this._cover.visible = true;
    });
    tw.onComplete.add(() => {
      lego.event.emit(ViewEvents.BoarView.CoverComplete);
    });
    tw.universal = true;
  }

  _eraseCell(uuid) {
    const cell = this.getCellByUuid(uuid);
    const { row, col } = cell;
    const trackEntry = this._cover.setAnimationByName(0, Spines.CardCover.Animations[`${row}${col}`], false);

    removeRunnable(this._eraseRunnable);
    this._eraseRunnable = delayRunnable(400, () => {
      lego.event.emit(ViewEvents.BoarView.EraseAnimationComplete, uuid);
    });
    trackEntry.timeScale = 1.8;
  }

  _onCellClick(uuid) {
    this._switchCellsInput(false);
    lego.event.emit(ViewEvents.BoarView.CellClick, uuid);
  }

  _onBoardStateUpdate(state) {
    switch (state) {
      case BoardState.Cover:
        this._switchCellsInput(false);
        CI_API.Globals.PARAMS.piece_pulse && this._bounceRightItems();
        this._showCover();
        break;
      case BoardState.Erase:
        this._switchCellsInput(false);
        break;
      case BoardState.Idle:
        this._switchCellsInput(true);
        break;

      default:
        break;
    }
  }

  _onCellStateUpdate(state, prevState, uuid) {
    switch (state) {
      case CellState.Erase:
        this._eraseCell(uuid);
        break;
      case CellState.Opened:
        this._onCardOpened(uuid);
        break;

      default:
        break;
    }
  }

  _onCardOpened(uuid) {
    const { boardModel } = store.game;
    const { winningType } = boardModel.config;
    const { item } = boardModel.getCellByUUID(uuid);
    if (item === winningType) {
      this._bounceBoard();
    }
  }

  _bounceItem(uuid) {
    this.getCellByUuid(uuid).bounceItem();
  }

  _bounceBoard() {
    const { x, y } = this.scale;

    return this.game.add
      .tween(this.scale)
      .to({ x: x + 0.1, y: y + 0.1 }, 300, Phaser.Easing.Cubic.InOut, true, 0, 0, true);
  }

  _onBoardCellsUpdate(cells) {
    const gap = 13;

    this._fxs = [];

    this._cells = cells.map((row, rowIndex) => {
      const rowArr = row.map((cellModel, colIndex) => {
        const { uuid, item } = cellModel;
        const cell = new CellView(uuid, { item, col: colIndex, row: rowIndex });
        const fx = new CellFx(uuid, { item, col: colIndex, row: rowIndex });
        cell.cellClick.addOnce(this._onCellClick, this);
        this.addChild(cell);

        this.addChild(fx);
        this._fxs.push(fx);

        const { width: cW, height: cH } = cell;
        const initialX = -this._cardFrame.width / 2 + gap + cW / 2;
        const initialY = -this._cardFrame.height / 2 + gap + cH / 2;

        cell.position.set(initialX + colIndex * (cW + gap), initialY + rowIndex * (cH + gap));
        fx.position.set(initialX + colIndex * (cW + gap), initialY + rowIndex * (cH + gap));

        return cell;
      });

      return rowArr;
    });

    this.bringToTop(this._cover);
    this._fxs.forEach(fx => {
      this.bringToTop(fx);
    });
  }

  _build() {
    this._buildCardFrame();
    this._buildCardCover();

    postRunnable(() => {
      const tw = showFromRight(this, 300);
      tw.onStart.add(() => {
        this.alpha = 1;
      });

      tw.onComplete.add(() => {
        lego.event.emit(ViewEvents.BoarView.ShowComplete);
      });
    });
  }

  _buildCardFrame() {
    const frame = makeImage(getCardFrameImageConfig());
    this.addChild((this._cardFrame = frame));
  }

  _buildCardCover() {
    const cover = makeSpine(getCardCoverSpineConfig(0, -11));
    cover.visible = false;
    this.addChild((this._cover = cover));
  }
}
