import { lego } from '@armathai/lego';
import { getPersistentCtaButtonConfig } from '../../configs/button-configs';
import { ViewEvents } from '../../events/view-events';
import { Button } from '../../utils/button/button';

export class PersistentCTAView extends Button {
  constructor() {
    super(getPersistentCtaButtonConfig());
    this.alpha = 0;
    this.onClick.add(() => lego.event.emit(ViewEvents.PersistentCtaView.Click), this);
  }
}
