/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGameGridConfig } from '../../configs/grid-configs';
import { getRightScratchItemEmitterConfig } from '../../configs/particles-configs';
import { CellState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { getGameBounds } from '../../utils';
import { ItemFountainEmitter } from '../../utils/emmitters/coin-fall/item-fountain-emitter';
import { BoardView } from './board/board-view';
import { MenuView } from './menu/menu-view';

export class GameView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    this._boardView = null;
    this._menuView = null;

    lego.event.on(ModelEvents.GameModel.MenuModelUpdate, this._onMenuUpdate, this);
    lego.event.on(ModelEvents.GameModel.BoardModelUpdate, this._onBoardUpdate, this);
    lego.event.on(ModelEvents.CellModel.StateUpdate, this._onCellStateUpdate, this);
  }

  get name() {
    return 'GameView';
  }

  getGridConfig() {
    return getGameGridConfig();
  }

  _onCellStateUpdate(state, prevState, uuid) {
    switch (state) {
      case CellState.Opened:
        this._onCardOpened(uuid);
        break;

      default:
        break;
    }
  }

  _onCardOpened(uuid) {
    const { boardModel } = store.game;
    const { winningType } = boardModel.config;
    const { item } = boardModel.getCellByUUID(uuid);
    if (item === winningType) {
      this._buildCoins();
    }
  }

  _buildCoins() {
    // @ts-ignore
    const localPos = this.toLocal({ x: 0, y: getGameBounds().bottom }, null);
    this._emitter = new ItemFountainEmitter(getRightScratchItemEmitterConfig(this, this._boardView.x, localPos.y));
    this._emitter.emit();
    this.addChild(this._emitter);
  }

  // BOARD
  _onBoardUpdate(board) {
    board ? this._buildBoard() : this._destroyBoard();
  }

  _buildBoard() {
    this.setChild('board', (this._boardView = new BoardView()));
  }

  _destroyBoard() {
    // this._boardView.destroy();
  }

  // MENU
  _onMenuUpdate(menuModel) {
    menuModel ? this._buildMenu(menuModel) : this._destroyMenu();
  }

  _buildMenu(menuModel) {
    this.setChild('game', (this._menuView = new MenuView(menuModel)));
  }

  _destroyMenu() {
    this._menuView.destroy();
  }
}
