import { InputPriority } from '../constants';
import {
  getLeftArrowDownImageConfig,
  getLeftArrowImageConfig,
  getRightArrowDownImageConfig,
  getRightArrowImageConfig
} from './image-configs';
import {
  getCtaLoseButtonPatchConfig,
  getCtaPlayButtonPatchConfig,
  getPersistentCtaButtonPatchConfig
} from './ninepatch-configs';
import { getCtaLoseButtonTextConfig, getCtaWinButtonTextConfig, getPersistentCtaTextConfig } from './text-configs';

// PERSISTENT CTA
export function getPersistentCtaButtonConfig() {
  return {
    input: {
      name: 'persistent_cta_button',
      priority: InputPriority.Game
    },
    states: {
      up: {
        bg: getPersistentCtaButtonPatchConfig(),
        label: getPersistentCtaTextConfig()
      }
    }
  };
}

// CTA
export function getCtaLoseButtonConfig(text) {
  return {
    input: {
      name: 'cta_lose_button',
      priority: InputPriority.Cta + 1
    },
    states: {
      up: {
        bg: getCtaLoseButtonPatchConfig(),
        label: getCtaLoseButtonTextConfig(text)
      }
    }
  };
}

export function getCtaWinPlayButtonConfig() {
  return {
    input: {
      name: 'green_btn',
      priority: InputPriority.Cta + 1
    },
    states: {
      up: {
        bg: getCtaPlayButtonPatchConfig(390, 90),
        label: getCtaWinButtonTextConfig()
      }
    }
  };
}

export function getRightArrowButtonConfig() {
  return {
    input: {
      name: 'right_button',
      priority: InputPriority.Foreground
    },
    states: {
      up: {
        bg: getRightArrowImageConfig()
      },
      down: {
        bg: getRightArrowDownImageConfig()
      }
    }
  };
}

export function getLeftArrowButtonConfig() {
  return {
    input: {
      name: 'right_button',
      priority: InputPriority.Foreground
    },
    states: {
      up: {
        bg: getLeftArrowImageConfig()
      },
      down: {
        bg: getLeftArrowDownImageConfig()
      }
    }
  };
}
