/* eslint-disable class-methods-use-this */
import { getLeftArrowButtonConfig, getRightArrowButtonConfig } from '../../../configs/button-configs';
import { Button } from '../../../utils/button/button';
import { Container } from '../../../utils/container';
// eslint-disable-next-line max-classes-per-file

// const defaultValues = {
//   width: 300,
//   height: 300,
//   gameObjects: [],
//   distance: 350,
//   direction: 'x',
//   maxSpeed: 3,
//   scaleIncrement: 0
// };

export class CarouselComponent extends Container {
  constructor(gameObjects) {
    super();

    this.nearestItemUpdate = new Phaser.Signal();
    this.onUp = new Phaser.Signal();
    this.onDown = new Phaser.Signal();
    this.focusOnNearestComplete = new Phaser.Signal();

    this._allowToDrag = true;
    this._nearestItemIndex = null;
    this._offset = 300;
    this._speed = 0;
    this._gameObjects = this._addGameObjects(gameObjects);

    this._positioningGameObjects();

    this._downPoint = new Phaser.Point();
    this._pointOldPosition = new Phaser.Point();
    this._startActionTime = null;
    this.game.input.onDown.add(this._onDown, this);
    this.game.input.onUp.add(this._onUp, this);

    this._width = this._calculateWidth();
    this.tileX = 0;
    this.focusOnNearest(true);

    this._buildArrows();
  }

  get nearestItem() {
    return this._gameObjects[this._nearestItemIndex];
  }

  get nearestItemIndex() {
    return this._nearestItemIndex;
  }

  get tileX() {
    return this._tileX;
  }

  get leftArrow() {
    return this._left;
  }

  get rightArrow() {
    return this._right;
  }

  set tileX(value) {
    this._tileX = value;
    this._updateGameObjectsPositions();
    this._updateGameObjectsParameters();
  }

  destroy() {
    this.game.input.onDown.remove(this._onDown, this);
    this.game.input.onUp.remove(this._onUp, this);
  }

  rebuild() {
    const { x } = this._gameObjects[this._nearestItemIndex];

    this._left.position.set(x - LP(500, 250), 0);
    this._right.position.set(x + LP(500, 250), 0);

    this._left.scale.set(LP(1.5, 1.5));
    this._right.scale.set(LP(1.5, 1.5));
  }

  update() {
    if (this._speed) {
      this.tileX += 5 * this._speed;
      this._speed -= (0.03 * this._speed) / Math.abs(this._speed);
      if (this._speed < 0.5 && this._speed > -0.5 && this._speed !== 0) {
        this._speed = 0;
        this.focusOnNearest();
      }
    }
  }

  focusOnItem(delta) {
    this._switchItemsInputs(false);
    let index = this._nearestItemIndex + delta;
    if (index > this._gameObjects.length - 1) {
      index = 0;
    } else if (index < 0) {
      index = this._gameObjects.length - 1;
    }

    this._nearestItemIndex = index;
    this.focusOnNearest();
  }

  focusOnNearest(force = false) {
    this._removeFocusTween();
    const dx = this._width / 2 - this._gameObjects[this._nearestItemIndex].x;
    if (force) {
      this.tileX += dx;
      this._gameObjects[this._nearestItemIndex].switchInput(true);
      this.focusOnNearestComplete.dispatch();
    } else {
      this._focusOnNearestItem = this.game.add
        .tween(this)
        .to({ tileX: this.tileX + dx }, 400, Phaser.Easing.Sinusoidal.Out, true);
      this._focusOnNearestItem.onComplete.add(this._onFocusTweenComplete, this);
    }
  }

  _buildArrows() {
    const right = new Button(getRightArrowButtonConfig());
    const left = new Button(getLeftArrowButtonConfig());

    this.addChild((this._right = right));
    this.addChild((this._left = left));

    left.name = 'left_arrow';
    left.switchEnable(true);
    left.onOut.add(this._onArrowOut, this);
    left.onClick.add(this._onLeftArrowClick, this);
    left.onPressed.add(this._onLeftArrowDown, this);

    right.name = 'right_arrow';
    right.switchEnable(true);
    right.onOut.add(this._onArrowOut, this);
    right.onClick.add(this._onRightArrowClick, this);
    right.onPressed.add(this._onRightArrowDown, this);
  }

  _onArrowOut() {
    this.game.input.deleteMoveCallback(this._onMove, this);
    this._allowToDrag = true;
    this._right.switchEnable(true);
    this._left.switchEnable(true);
  }

  _onLeftArrowDown() {
    this.game.input.deleteMoveCallback(this._onMove, this);
    this._allowToDrag = false;
    this._right.switchEnable(false);
  }

  _onRightArrowDown() {
    this.game.input.deleteMoveCallback(this._onMove, this);
    this._allowToDrag = false;
    this._left.switchEnable(false);
  }

  _onLeftArrowClick() {
    this.game.input.deleteMoveCallback(this._onMove, this);
    this._allowToDrag = false;
    this._left.switchEnable(false);
    this._right.switchEnable(false);
    this.focusOnItem(-1);
  }

  _onRightArrowClick() {
    this.game.input.deleteMoveCallback(this._onMove, this);
    this._allowToDrag = false;
    this._left.switchEnable(false);
    this._right.switchEnable(false);
    this.focusOnItem(1);
  }

  _onFocusTweenComplete() {
    this._allowToDrag = true;
    this._left.switchEnable(true);
    this._right.switchEnable(true);

    this.focusOnNearestComplete.dispatch();
    this._gameObjects[this._nearestItemIndex].switchInput(true);
  }

  _removeFocusTween() {
    !!this._focusOnNearestItem && this._focusOnNearestItem.onComplete.remove(this._onFocusTweenComplete, this);
    this.game.tweens.remove(this._focusOnNearestItem);
    this.game.tweens.removeFrom(this);
    this._focusOnNearestItem = null;
  }

  _onDown(pt) {
    if (this._allowToDrag) {
      this._removeFocusTween();

      this._speed = 0;
      this._downPoint.set(pt.x, pt.y);
      this._pointOldPosition.set(pt.x, pt.y);
      this._startActionTime = Date.now();
      this.game.input.addMoveCallback(this._onMove, this);
      this.onDown.dispatch();
    }
  }

  _onUp(pt) {
    if (this._allowToDrag) {
      this.game.input.deleteMoveCallback(this._onMove, this);
      const { x } = this._gameObjects[this._nearestItemIndex];
      if (this._dragging || x !== this._width / 2) {
        const deltaX = pt.x - this._downPoint.x;
        const direction = Phaser.Math.sign(deltaX);
        this._speed = direction * Math.min(Math.abs(deltaX) / (Date.now() - this._startActionTime), 5);
        if (this._speed === 0) {
          this.focusOnNearest();
        }
      } else {
        this.focusOnNearest(true);
      }
      this.onUp.dispatch();
      this._dragging = false;
    }
  }

  _onMove(pt) {
    if (this._allowToDrag) {
      if (
        Math.abs(this._downPoint.x - pt.x) > 10 ||
        Math.abs(this.nearestItem.x - this._width / 2) > 10 ||
        this._dragging
      ) {
        this._dragging = true;
        this._switchItemsInputs(false);
        const difX = pt.x - this._pointOldPosition.x;
        this._pointOldPosition.x = pt.x;
        this.tileX += difX / this.scale.x;
      }
    }
  }

  _switchItemsInputs(value) {
    this._gameObjects.forEach(gameObject => {
      gameObject.switchInput(value);
    });
  }

  _calculateWidth() {
    return this._gameObjects.reduce(
      acc => acc + this._offset,
      this._gameObjects[0].width - this._offset + (this._offset - this._gameObjects[0].width)
    );
  }

  _positioningGameObjects() {
    for (let i = 0; i < this._gameObjects.length; i += 1) {
      const gameObject = this._gameObjects[i];

      this._setGameObjectX(gameObject, i * this._offset);

      this._checkForLimits(gameObject);
    }
  }

  _updateGameObjectsPositions() {
    for (let i = 0; i < this._gameObjects.length; i += 1) {
      const gameObject = this._gameObjects[i];
      this._setGameObjectX(gameObject, (this._tileX % this._width) + i * this._offset);

      this._checkForLimits(gameObject);
    }
  }

  _checkForLimits(gameObject) {
    if (gameObject.x > this._width) {
      this._setGameObjectX(gameObject, gameObject.x - this._width);
    } else if (gameObject.x < 0) {
      this._setGameObjectX(gameObject, gameObject.x + this._width);
    }
  }

  _updateGameObjectsParameters() {
    let nearestItemIndex = null;
    let nearestItemLayer = 0;
    for (let i = 0; i < this._gameObjects.length; i += 1) {
      const gameObject = this._gameObjects[i];
      const radius = 1 - Math.abs(this._width * 0.5 - gameObject.x) / (this._width * 0.5);

      this._updateGameObjectScale(gameObject);
      this._updateGameObjectAlpha(gameObject);
      this._updateGameObjectDim(gameObject);

      const layer = Math.floor(this._gameObjects.length * radius);
      // gameObject.alpha = scaleFactory;
      this.addChildAt(gameObject, layer);
      this._left && this.bringToTop(this._left);
      this._left && this.bringToTop(this._right);
      if (layer >= nearestItemLayer && nearestItemIndex !== i) {
        nearestItemLayer = layer;
        nearestItemIndex = i;
      }
    }

    if (this._nearestItemIndex !== nearestItemIndex) {
      this.nearestItemUpdate.dispatch(this._gameObjects[nearestItemIndex].uuid);
      this._nearestItemIndex = nearestItemIndex;
    }
  }

  _updateGameObjectScale(gameObject) {
    if (gameObject.x < this._width * 0.3) {
      const scaleFactory = 1 - 0.5 * (Math.abs(this._width * 0.3 - gameObject.x) / (this._width * 0.3));
      gameObject.scale.set(scaleFactory);
    } else if (gameObject.x > this._width * 0.7) {
      const scaleFactory = 1 - 0.5 * (Math.abs(gameObject.x - this._width * 0.7) / (this._width - this._width * 0.7));
      gameObject.scale.set(scaleFactory);
    } else {
      const scaleFactory = 1;
      gameObject.scale.set(scaleFactory);
    }
  }

  _updateGameObjectAlpha(gameObject) {
    if (gameObject.x < this._width * 0.1) {
      const alphaFactory = 1 - Math.abs(this._width * 0.1 - gameObject.x) / (this._width * 0.1);
      gameObject.alpha = alphaFactory;
    } else if (gameObject.x > this._width * 0.9) {
      const alphaFactory = 1 - (gameObject.x - this._width * 0.9) / (this._width - this._width * 0.9);
      gameObject.alpha = alphaFactory;
    } else {
      const alphaFactory = 1;
      gameObject.alpha = alphaFactory;
    }
  }

  _updateGameObjectDim(gameObject) {
    if (gameObject.x < this._width * 0.3) {
      const dimFactory = 1 - Math.abs(this._width * 0.3 - gameObject.x) / (this._width * 0.3);
      gameObject.updateDim(1 - dimFactory);
    } else if (gameObject.x > this._width * 0.7) {
      const dimFactory = 1 - (gameObject.x - this._width * 0.7) / (this._width - this._width * 0.7);
      gameObject.updateDim(1 - dimFactory);
    } else {
      const dimFactory = 1;
      gameObject.updateDim(1 - dimFactory);
    }
  }

  _addGameObjects(gameObjects) {
    gameObjects.forEach((gameObject, index) => {
      this.addChildAt(gameObject, index);
      gameObject.onClick.add(this._gameObjectClick, this);
    });

    return gameObjects;
  }

  _gameObjectClick() {
    this.game.input.onDown.remove(this._onDown, this);
    this.game.input.onUp.remove(this._onUp, this);
  }

  _setGameObjectX(gameObject, value) {
    gameObject.x = value;
  }
}
